.CreditCardForm {
  margin-top: 200px;
  position: relative;
}

.container.segment {
  border-radius: 10px;
}

form {
  margin-top: 180px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input {
  height: 45px;
}

select.ui.dropdown {
  height: 45px;
}

.CreditCard.front{visibility:visible}.CreditCard.back{visibility:hidden}.CreditCard{margin-top:200px;display:flex;justify-content:center;position:absolute;top:-151px;left:50%;-webkit-transform:translateX(-240px);transform:translateX(-240px);z-index:2;color:white;font-family:"Source Code Pro", monospace}.CreditCard .ui.card{height:302px;width:480px;border-radius:20px;background:url(/credit-card-form/static/media/background.85d9c345.jpg);box-shadow:none;position:relative}.CreditCard .ui.card .ccType{position:absolute;right:35px;top:35px}.CreditCard .ui.card .master{width:100px}.CreditCard .ui.card .visa{width:120px}.CreditCard .ui.card .amex{width:150px}.CreditCard .ui.card .discover{width:170px}.CreditCard .ui.card .chip{width:70px;position:absolute;left:35px;top:80px}.CreditCard .ui.card .ccNumber{display:flex;justify-content:space-evenly;width:100%;position:absolute;top:165px;font-size:30px}.CreditCard .ui.card .name-date{margin:210px 40px 0 40px;display:flex;justify-content:space-between}.CreditCard .ui.card .name-date .cardHolder .holderTitle{font-size:16px;color:lightgray;margin-bottom:8px}.CreditCard .ui.card .name-date .cardHolder .holderName{font-size:22px}.CreditCard .ui.card .name-date .expirationDate .expirationTitle{font-size:16px;color:lightgray;margin-bottom:8px}.CreditCard .ui.card .name-date .expirationDate .slash{margin:0 5px}.CreditCard .ui.card .name-date .expirationDate .date{display:flex;font-size:22px}

.CreditCardBack.front{visibility:hidden}.CreditCardBack.back{visibility:visible}.CreditCardBack{margin-top:200px;display:flex;justify-content:center;position:absolute;top:-151px;left:50%;-webkit-transform:translateX(-240px);transform:translateX(-240px);color:white;font-family:"Source Code Pro", monospace}.CreditCardBack .ui.card{height:302px;width:480px;border-radius:20px;background:url(/credit-card-form/static/media/background.85d9c345.jpg);box-shadow:none;position:relative}.CreditCardBack .ui.card .blackspace{background-color:black;width:100%;height:50px;margin-top:35px}.CreditCardBack .ui.card .cvv{position:absolute;right:0;margin-right:25px;top:110px;font-size:20px;font-family:"Source Code Pro", monospace}.CreditCardBack .ui.card .whitespace{background-color:white;width:93%;height:50px;border-radius:4px;margin:50px auto 0 auto;display:flex;align-items:center}.CreditCardBack .ui.card .whitespace .cvvNum{color:black;margin-left:405px}.CreditCardBack .ui.card .ccType{margin-top:30px;opacity:0.6}.CreditCardBack .ui.card .visa{margin-left:360px;width:100px}.CreditCardBack .ui.card .master{margin-top:20px;margin-left:370px;width:90px}.CreditCardBack .ui.card .discover{margin-left:310px;width:150px}.CreditCardBack .ui.card .amex{margin-left:325px;width:130px}

